<script>

import Radar from './radar.vue'

import Qq from "./outline/qq.vue"

let time = ''

export default {
    name: 'Main',
    components:{
            Radar,
            Qq
    },
    data(){
        return {
            mes: true,
            buttontext: '查看视图',
            start: 0,
            end: 0,
            project: [
                // {name: "房品优选小程序",link: 'http://fangpinyouxuan.com/z.html',img: '/project/fpyx.jpg'},
                // {name: "房品优选后台管理",link: 'https://fangpinyouxuan.com/login',img: '/project/fpyxht.png'},
                // {name: "白泽智慧管理平台",link: 'http://admin.baizetech.com.cn/login',img: '/project/zhnmht.png'},
                // {name: "白泽智慧管理平台数据大屏展示",link: 'https://zhnm.yzf.asia/',img: '/project/zhnmdp.png'},
                // {name: "白泽智慧农贸小程序",link: '',img: '/project/zhnm.jpg'},
                // {name: "景曰小程序",link: '',img: '/project/jy.jpg'},
                // {name: "白泽科技有限公司官网",link: 'https://www.baizetech.com.cn',img: '/project/bzgw.png'}
                {name: 1,img: '/project/gzhcode.jpg'},
                {name: 2,img: '/project/gzhcode.jpg'},
                {name: 3,img: '/project/gzhcode.jpg'},
                {name: 4,img: '/project/gzhcode.jpg'},
                {name: 5,img: '/project/gzhcode.jpg'},
                {name: 6,img: '/project/gzhcode.jpg'}
            ],
            type:[
                {name: "厚德",style:"color: #d3d30d"},
                {name: "唯实",style:"color: #6bc30d"},
                {name: "诚信",style:"color: #0dcbd3"},
                {name: "好学",style:"color: #b9ac74"}
            ],
            dialogVisible: false,
            title: '',
            img: '',

            move: false
        }
    },
    mounted(){

        // console.log(this.$echarts,"打印信息")

        window.addEventListener('mousewheel',this.handleScroll);//mousewheel鼠标滚动事件

        time = setTimeout(() => {
            document.addEventListener('touchstart',this.touchstart);
            document.addEventListener('touchmove',this.touchmove);
            document.addEventListener('touchend',this.touchend); 
        },4500)

        
    },
    methods:{
        handleScroll(e){
            // console.log(e,"滚动")
            let direction = e.deltaY > 0 ? 'down':'up';  //deltaY为正则滚轮向下，为负滚轮向上
            if(direction=='down' && e.deltaY >= 100){ //125为用户一次滚动鼠标的wheelDelta的值
                this.$refs.goupdown.next()
            }
            if(direction=='up' && e.deltaY <= -100){
                this.$refs.goupdown.prev()
            }
        },

        touchstart(e){
            // console.log(e.changedTouches[0].pageY,"开始时鼠标的位置")
            this.move = false
            this.start = e.changedTouches[0].pageY
        },
        touchmove(){
            this.move = true
        },
        touchend(e){
            console.log(this.end - this.start,"结束时鼠标的位置")
            this.end = e.changedTouches[0].pageY
            if(this.move){
                if(this.end - this.start > 0){//结束y坐标 - 开始坐标 > 0 向上翻页 < 0向下翻页
                    this.$refs.goupdown.prev()
                }else{
                    this.$refs.goupdown.next()
                }
            }else{
                console.log("点击屏幕")
            }
            
        },


        // 打开新网页
        gonew(e){
            console.log(e,"查看项目")
            if(e.link == ''){
                const h = this.$createElement;
                this.$message({
                message: h('div',{ style: 'font-size: 16px' },'可通过微信查询小程序名称查看')
                });
            }else{
                window.open(e.link)
            }
        },
        gocsdn(a){
            if(a == 0){
                window.open("https://blog.csdn.net/a_115098?spm=1011.2124.3001.5343&type=blog")
            }else if(a == 1){
                this.dialogVisible = true
                this.title = "扫描二维码添加QQ好友"
                this.img = "/project/qqcode.png"
            }else if(a == 2){
                this.dialogVisible = true
                this.title = "扫描二维码添加微信好友"
                this.img = "/project/wxcode.jpg"
            }else if(a == 3){
                this.dialogVisible = true
                this.title = "扫描二维码关注公众号"
                this.img = "/project/gzhcode.jpg"
            }else if(a == 4){
                this.dialogVisible = true
                this.title = "小红书号：577007118"
                this.img = '/project/xhscode.png'
            }
            
            
            
        },
        seemes(){
            this.mes = !this.mes
            if(this.mes){
                this.buttontext = "查看视图"
            }else{
                this.buttontext = "查看简介"
            }
        }
    },
    beforeDestroy() {
        clearInterval(time)
    }
}
</script>

