<template>
   <div class="radarcharts">
    
      <div class="peoplechart" ref="radarcharts"></div>

   </div>
</template>

<script>


export default {
   name: "radarcharts",
   data() {
       return {

       }
   },
   created() {

   },
   mounted() {
    // 六指标雷达图
    const  radarcharts = this.$echarts.init(this.$refs.radarcharts)
    let option = {
        tooltip: {},
        radar: {
            // shape: 'circle',
            // formatter: '{value}',
              nameGap: 10,

               triggerEvent: true,
                splitNumber: 4,
                splitArea: {
                    areaStyle: {
                        color: ['#546b9d','#4e5f95','#44548f','#3b4783d0'], //圆环内层颜色
                        shadowColor: ['#05688e','#086495','#073b6a','#07325e'], // 圆颜色
                        shadowBlur: 30,
                        opacity: 0.5
                    }
                },
                axisLine: {
                    lineStyle: {
